
import { PodcastClass, EpisodeClass } from "../core/types";

import podcasts from '../assets/data/podcasts.json';
import episodes from '../assets/data/episodes.json';

export const getPodcasts = (): PodcastClass[] => podcasts.map(p => new PodcastClass(p));

export const getPodcast = (id: number): PodcastClass | null => {
  const podcast = podcasts.find(p => p.id === id);

  return podcast ? new PodcastClass(podcast) : null;
}

export const getEpisodes = (): EpisodeClass[] => episodes.map(e => new EpisodeClass(e));

export const getEpisode = (id: number): EpisodeClass | null => {
  const episode = episodes.find(p => p.id === id);

  return episode ? new EpisodeClass(episode) : null;
}
