import React from "react";
import { Podcasts, Episodes } from "../../components";
import { getPodcasts, getEpisodes } from "../../services/podcastService";

import s from "./HomePage.module.css";

const HomePage = () => {
  const podcastList = getPodcasts();
  const episodesList = getEpisodes();

  return (
    <div className={s.root}>
      <h2>Podcasts</h2>
      <Podcasts list={podcastList} />

      <h2>Episodes</h2>
      <Episodes list={episodesList} />
    </div>
  );
};

export default HomePage;
