import React from "react";
import { Match } from "../../core/types";
import { getIdFromMatch } from "../../core/utils";
import { getEpisode } from "../../services/podcastService";
import { Item } from "../../components";
import s from "./EpisodePage.module.css";

type Props = {
  match: Match;
};

const EpisodePage = ({ match }: Props) => {
  const id: number = getIdFromMatch(match) || 0;
  const item = getEpisode(id);
  if (!item) {
    return <div>Episode not found</div>;
  }

  const { podcastTitle, title, description, duration, created } = item;
  const date = new Date(created);

  return (
    <div className={s.root}>
      <h2>Episode</h2>

      <Item label="Podcast" value={podcastTitle} />
      {/* <Item label="Id" value={id.toString()} /> */}
      <Item label="Title" value={title} />
      <Item label="Description" value={description} />
      <Item label="Duration" value={`${duration} seconds`} />
      <Item label="Created" value={date.toLocaleString()} />
    </div>
  );
};

export default EpisodePage;
