import React from "react";
import { PodcastClass } from "../../core/types";
import { Podcast } from "../../components";
import s from "./Podcasts.module.css";

type Props = {
  list: PodcastClass[];
};

// ----------
// COMPONENT:
const Podcasts = ({ list }: Props) => {
  return (
    <div className={s.root}>
      {list.map(item => (
        <Podcast key={item.id} item={item} />
      ))}
    </div>
  );
};

export default Podcasts;
