
export class PodcastClass {
  id: number;
  title: string;
  numberOfEpisodes: number;
  description: string;
  created: string;

  constructor(data: any) {
    this.id = data.id;
    this.title = data.title;
    this.numberOfEpisodes = data.numberOfEpisodes;
    this.description = data.description;
    this.created = data.created;
  }
};

export class EpisodeClass {
  id: number;
  title: string;
  description: string;
  podcastId: number;
  podcastTitle: string;
  duration: number;
  created: string;

  constructor(data: any) {
    this.id = data.id;
    this.title = data.title;
    this.description = data.description;
    this.podcastId = data.podcast.id;
    this.podcastTitle = data.podcast.title;
    this.duration = data.duration;
    this.created = data.created;
  }
};

type Params = {
  id: string;
};

export type Match = {
  params: Params;
};
