import React from "react";
import { Route, Switch } from "react-router-dom";
import { HomePage, PodcastPage, EpisodePage } from "./pages";
import "./App.css";

const route = (path: string, page: any) => <Route path={path}>{page}</Route>;

function App() {
  return (
    <div className="app">
      <header className="appHeader">
        <a className="logo" href="/">
          &nbsp;
        </a>
      </header>

      <Switch>
        <Route
          path="/podcast/:id"
          render={({ match }) => <PodcastPage match={match} />}
        />

        <Route
          path="/episode/:id"
          render={({ match }) => <EpisodePage match={match} />}
        />

        {route("/", <HomePage />)}
      </Switch>
    </div>
  );
}

export default App;
