import React from "react";
import { Match } from "../../core/types";
import { getIdFromMatch } from "../../core/utils";
import { getPodcast } from "../../services/podcastService";
import { Item } from "../../components";
import s from "./PodcastPage.module.css";

type Props = {
  match: Match;
};

const PodcastPage = ({ match }: Props) => {
  const id: number = getIdFromMatch(match) || 0;
  const item = getPodcast(id);
  if (!item) {
    return <div>Podcast not found</div>;
  }

  const { title, numberOfEpisodes, description, created } = item;
  const date = new Date(created);

  return (
    <div className={s.root}>
      <h2>Podcast</h2>
      {/* <Item label="Id" value={id.toString()} /> */}
      <Item label="Title" value={title} />
      <Item label="Number of episodes" value={numberOfEpisodes.toString()} />
      <Item label="Description" value={description} />
      <Item label="Created" value={date.toLocaleString()} />
    </div>
  );
};

export default PodcastPage;
